  line-height: 28px;
  color: #232536;
  opacity: 0.87;
}

.reviewer__description {
  margin: 0;
  padding: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 20px;
  color: #232536;
  opacity: 0.6;
}

.reviews__swiper-container {
  position: relative;
  padding-bottom: 50px;
  max-width: 595px;
  margin: 0 auto;
}
.reviews__swiper-container::before {
  position: absolute;
  content: "";
  left: -16px;
  top: 0;
  width: 16px;
  height: 340px;
  background-image: url("../../img/shapes-reviews.svg");
}
@media (max-width: 767px) and (max-width: 767px) {
  .reviews__swiper-container::before {
    display: none;
  }
}
.reviews__swiper-container::after {
  position: absolute;
  content: "";
  top: -21px;
  right: 25px;
  width: 43px;
  height: 30px;
  background-image: url("../../img/quote.svg");
  z-index: 2;
}

.footer {
  padding-top: 23px;
  padding-bottom: 28px;
  background-color: #FFE6D2;
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__content-wrapper {
  display: flex;
  align-items: center;
}
@media (max-width: 767px) and (max-width: 767px) {
  .footer__content-wrapper {
    flex-direction: column;
    align-items: start;
  }
}

.footer__logo {
  color: #232536;
  margin-right: 94px;
}

.footer__copyright {
  margin: 0;
  margin-top: 7px;
  padding: 0;
}

.footer__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.footer__item:not(:last-child) {
  margin-right: 27px;
}

